/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4814814815vw;
  }
}
@media (min-width: 1080px) {
  html {
    font-size: 16px;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeLegibility;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

label {
  position: relative;
}

input[type=checkbox],
input[type=radio] {
  opacity: 0;
  position: absolute;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}

@media (any-hover: hover) {
  a:hover {
    opacity: 0.6;
  }
}
/* 矢印のクリック範囲 */
.arrow-button {
  display: none;
}
@media screen and (min-width: 768px) {
  .arrow-button {
    background-color: transparent;
    height: 5rem;
    transition: 0.2s;
    width: 5rem;
    display: grid;
    place-content: center;
    top: -4.125rem;
  }
}

.arrow-button.prev {
  left: 0;
}

.arrow-button.next {
  left: 10rem;
}

.arrow-button::before {
  content: "";
  width: 5rem;
  height: 5rem;
  border: 1px solid #408F95;
  border-radius: 50%;
}

.arrow-button__tip {
  width: 2.5rem;
  height: 0.0625rem;
  background-color: #408F95;
  position: absolute;
  top: 51%;
  left: 50%;
  transform: translateX(-50%);
}

.arrow-button__tip::after {
  content: "";
  display: block;
  width: 0.625rem;
  height: 0.0625rem;
  background-color: #408F95;
  position: absolute;
}

.arrow-button__tip--prev::after {
  left: -0.0625rem;
  top: -0.1875rem;
  rotate: -40deg;
}

.arrow-button__tip--next::after {
  right: -0.0625rem;
  top: -0.1875rem;
  rotate: 40deg;
}

